.page {
	height: 100vh;
	position: fixed;
	width: 100%;
	top: 0;
	transition: opacity 0.5s ease-in-out;
}

.page-enter {
	opacity: 0;
}

.page-enter-active {
	opacity: 1;
}

.page-exit-active {
	opacity: 0;
}
